<template>
  <v-btn>
    {{ getValue(element, 'Title') }}
  </v-btn>
</template>

<script>
import {
  ElementModel,
  PropertyModel,
  StateModel,
  Types
} from "./ElementModel.vue";
import { getValue, setValue } from "./store";

export default {
  name: "button-element",
  props: ["element"],
  created() {
    this.$store.commit("addProperties", {
      element: this.element,
      properties: [
        new PropertyModel("Title", "Click me!")
      ]
    });
    this.setValue("Width", "auto");
    this.setValue("Height", "auto");
  },
  methods: {
    getValue,
    setValue
  }
};
</script>

<style scoped>

</style>



